body {
	background-color: black;
	color: white
}

.App {
	text-align: center;
	font-size: 35px;
	margin-top: 20%;
}

.App-title {
  font-size: 50px;
}

.App-form {
	margin-top: 10px;
	width: 300px;
	display: inline-block;
}

.Clock-days, .Clock-hours, .Clock-minutes, .Clock-seconds {
	display: inline;
	margin: 10px;
}

.Clock-time-validation-error {
	font-size: 16px;
	margin: 0;
}

/* Copyright */

.Copyright {
	text-align: center;
	width: 100%;
	position: absolute;
	bottom: 10px
}



